.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

div.tui-pagination {
  height: 38px;
  @media screen and (min-width: 768px) {
    height: 44px;
  }
  margin-bottom: 40px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 38px;
  height: 38px;
  @media screen and (min-width: 768px) {
    width: 44px;
    height: 44px;
  }
  background-color: transparent;
  border: 1px solid var(--color-black);

  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-decoration: none;
  vertical-align: middle;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &.tui-last {
    background-color: var(--color-yellow);
    border: 1px solid var(--color-black);
  }
  &.tui-next {
    background-color: var(--color-yellow);
    border: 1px solid var(--color-black);
  }
  &.tui-prev {
    background-color: var(--color-yellow);
    border: 1px solid var(--color-black);
  }
  &.tui-first {
    background-color: var(--color-yellow);
    border: 1px solid var(--color-black);
  }
  &:hover,
  &:focus {
    background-color: var(--color-black);
    color: var(--color-white);
    border: 1px solid var(--color-black);
    outline: none;
  }
}

.tui-page-btn {
  color: var(--color-black);
}

.tui-is-disabled {
  background-color: var(--color-black);
  color: var(--color-white);
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  background-color: var(--color-black);
  color: var(--color-white);
}

.tui-pagination .tui-first-child.tui-is-selected {
  border: 1px solid var(--color-black);
}

.tui-pagination .tui-last-child.tui-is-selected {
  border: 1px solid var(--color-black);
}

.tui-pagination .tui-is-selected:hover {
  background-color: var(--color-black);
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  padding: 0;
  font-size: 0;
  white-space: nowrap;
  font-weight: normal;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding: 0;
  line-height: 1;
}

// .tui-pagination .tui-prev {
//   margin-right: 10px;
// }

// .tui-pagination .tui-next {
//   margin-left: 10px;
// }

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

// .tui-pagination .tui-is-disabled:hover {
//   background: #fff;
//   cursor: default;
// }

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: var(--color-black);
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

.tui-pagination .tui-prev {
  margin-right: 14px;
  @media screen and (min-width: 768px) {
    margin-right: 24px;
  }
}

.tui-pagination .tui-next {
  margin-left: 14px;
  @media screen and (min-width: 768px) {
    margin-left: 24px;
  }
}

.tui-pagination .tui-page-btn.tui-last {
  margin-left: 5px;
  @media screen and (min-width: 768px) {
    margin-left: 8px;
  }
}

.tui-pagination .tui-page-btn.tui-first {
  margin-right: 5px;
  @media screen and (min-width: 768px) {
    margin-right: 8px;
  }
}
.tui-last-child {
  margin-left: 5px;
  @media screen and (min-width: 768px) {
    margin-left: 8px;
  }
}

.tui-first-child {
  margin-right: 5px;
  @media screen and (min-width: 768px) {
    margin-right: 8px;
  }
}
